.main-table-container {
    position: relative;
}

/*Loader wrap*/

.loader-wrapper {
    margin: auto;
    width: 100px;
    margin-top: 80px;
}

.loader-wrapper p {
    color: red;
    font-weight: bold;
    font-size: 20px;
}

/*Select*/

select {
    width: 130px;
    border-radius: 5px;
    color: #ffffff;
    float: right;
    background-color: #273142;
    padding: 5px 5px;
    border: 1px solid #374558;   
    cursor: pointer;
}



